    flex-direction: row;
    height: auto;
    min-height: 380px;
    overflow: hidden;
    max-width: 100%;
}

html[lang="en"] .event-image {
    width: 380px;
    min-width: 380px;
    height: 380px;
    min-height: 380px;
    flex-shrink: 0;
}

html[lang="en"] .event-content {
    padding: 30px;
    height: 100%;
    overflow: hidden;
    max-width: calc(100% - 380px);
}

html[lang="en"] .event-content h2 {
    font-size: 1.2rem;
    line-height: 1.3;
    -webkit-line-clamp: 2;
    margin: 0 0 8px;
    padding-right: 70px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    word-break: break-word;
    max-width: 100%;
}

html[lang="en"] .event-status {
    position: absolute;
    top: 15px;
    right: 15px;
    padding: 5px 10px;
    font-size: 0.7rem;
    z-index: 10;
}

html[lang="en"] .event-description {
    font-size: 0.8rem;
    line-height: 1.4;
    -webkit-line-clamp: 2;
    margin-bottom: 10px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    word-break: break-word;
    max-width: 100%;
}

html[lang="en"] .event-brief-info {
    margin-top: 5px;
    margin-bottom: 10px;
    width: 100%;
    flex-grow: 1;
    overflow: hidden;
    max-width: 100%;
}

html[lang="en"] .sessions-list {
    gap: 4px;
