    display: flex;
    justify-content: flex-end;
}

html[lang="en"] .register-button {
    font-size: 0.8rem;
    padding: 5px 14px;
}

@media (max-width: 1024px) {
    html[lang="en"] .event-content h2 {
        font-size: 1.1rem;
    }
    
    html[lang="en"] .event-description {
        font-size: 0.75rem;
    }
    
    html[lang="en"] .brief-item,
    html[lang="en"] .brief-item span {
        font-size: 0.7rem;
    }
}

@media (max-width: 768px) {
    html[lang="en"] .event-card {
        flex-direction: column;
        height: auto;
        min-height: unset;
    }
    
    html[lang="en"] .event-image {
        width: 100%;
        min-width: 100%;
        height: 0;
        min-height: unset;
        padding-bottom: 100%;
        position: relative;
    }
    
    html[lang="en"] .event-image img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    
    html[lang="en"] .event-content {
        padding: 20px;
        height: auto;
        max-width: 100%;
    }
    
    html[lang="en"] .event-content h2 {
        font-size: 1.1rem;
        padding-right: 60px;
        margin-top: 0;
    }
    
    html[lang="en"] .event-description {
        font-size: 0.75rem;
        margin-bottom: 10px;
    }
    
    html[lang="en"] .sessions-list {
        max-height: none;
    }
    
    html[lang="en"] .session-item {
        flex-direction: column;
        gap: 5px;
        padding-bottom: 8px;
    }
    
    html[lang="en"] .brief-item {
        margin-right: 0;
        margin-bottom: 2px;
        min-width: auto;
        width: 100%;
    }
    
    html[lang="en"] .brief-item span {
        max-width: none;
        width: calc(100% - 25px);
    }
    
    html[lang="en"] .event-brief-info {
        margin-bottom: 10px;
    }
    
    html[lang="en"] .event-footer {
        margin-top: 8px;
    }
}

@media (max-width: 480px) {
    html[lang="en"] .event-content {
        padding: 15px;
    }
    
    html[lang="en"] .event-content h2 {
        font-size: 1rem;
        padding-right: 50px;
    }
    
    html[lang="en"] .event-description {
        font-size: 0.7rem;
        margin-bottom: 8px;
    }
    
    html[lang="en"] .event-brief-info {
        margin-bottom: 8px;
    }
    
    .event-image {
        height: 200px;
        min-height: 200px;
    }
}

/* 為日期和地點添加特定樣式 */
.session-heading {
    font-size: 1rem;
    font-weight: 600;
    color: #333;
    margin-bottom: 8px;
    border-left: 3px solid #142613;
    padding-left: 10px;
}

/* 移除不可見間隔元素 */
.event-brief-info::before {
    display: none;
}

/* 添加漸變陰影效果，提高可讀性 */
.event-image::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 30%;
    background: linear-gradient(to top, rgba(0,0,0,0.1), transparent);
    z-index: 1;
    pointer-events: none;
}