.partners-wrapper {
    background-color: #fff;
    margin: 0;
    padding: 0;
    position: relative;
    z-index: 1;
}

.partners-section {
    padding: 80px 0 0;
    background-color: #fff;
    margin-bottom: 0;
    position: relative;
}

.partners-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.partners-section::after {
    display: none;
}

.partners-title {
    text-align: center;
    font-size: 2.5rem;
    color: #1B3C35;
    margin-bottom: 50px;
    font-weight: 600;
}

.partners-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 40px;
    justify-items: center;
    align-items: center;
    margin-bottom: 0;
    padding-bottom: 120px;
}

.partner-item {
    width: 100%;
    max-width: 200px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.3s ease;
}

.partner-item:hover {
    transform: translateY(-8px);
}

.partner-logo {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    transition: all 0.3s ease;
}

/* 響應式設計 */
@media (max-width: 768px) {
    .partners-section {
        padding: 60px 0 0;
        margin-bottom: 0;
    }

    .partners-container {
        padding: 0 20px 40px;
    }

    .partners-title {
        font-size: 2rem;
        margin-bottom: 40px;
    }

    .partners-grid {
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
        gap: 30px;
        padding-bottom: 80px;
    }

    .partner-item {
        max-width: 150px;
        height: 80px;
    }
}

@media (max-width: 480px) {
    .partners-section {
        padding: 40px 0 0;
        margin-bottom: 0;
    }

    .partners-container {
        padding: 0 20px 30px;
    }

    .partners-title {
        font-size: 1.8rem;
        margin-bottom: 30px;
    }

    .partners-grid {
        grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
        gap: 20px;
        padding-bottom: 60px;
    }

    .partner-item {
        max-width: 120px;
        height: 60px;
    }
}

/* 針對 402*874 尺寸的中小型手機設備優化 */
@media (max-width: 420px) {
    .partners-section {
        padding: 30px 0 0;
    }

    .partners-container {
        padding: 0 15px 20px;
    }

    .partners-title {
        font-size: 1.7rem;
        margin-bottom: 25px;
    }

    .partners-grid {
        grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
        gap: 15px;
        padding-bottom: 40px;
    }

    .partner-item {
        max-width: 100px;
        height: 50px;
    }
    
    .partner-item img {
        max-height: 45px;
    }
}

/* 針對 500-850px 中等屏幕的設備，保持與桌面版相同的排版設計 */
@media (min-width: 500px) and (max-width: 850px) {
    .partners-section {
        padding: 80px 0 0;
    }

    .partners-container {
        padding: 0 20px;
    }

    .partners-title {
        font-size: 2.5rem;
        margin-bottom: 50px;
    }

    .partners-grid {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
        gap: 40px;
        padding-bottom: 120px;
    }

    .partner-item {
        max-width: 200px;
        height: 100px;
    }
}

.section-header h2 {
    font-size: 2.5rem;
    color: #1B3C35;
    margin: 0;
    font-weight: 600;
} 