.home-container {
  width: 100%;
  overflow-x: hidden;
  padding-top: 5.5rem;  /* Added padding-top to match navbar height */
  position: relative;
}

.home-page {
    width: 100%;
    min-height: 100vh;
}

.home-hero {
    width: 100%;
    height: 100vh;
    background-image: url('../assets/landingcity.jpg');
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 10%;
    animation: gradualFade 1.5s ease-in;
}

/* 漸入動畫 */
@keyframes gradualFade {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

/* 文字浮現效果 */
.hero-text-group {
    display: flex;
    gap: 30px;  /* 增加間距 */
    align-items: center;  /* 垂直置中對齊 */
}

.hero-text-large {
    font-size: 9rem;  /* 基本大小 */
    font-weight: bold;
    margin: 0;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    animation: slideInLeft 1s ease-out;
    opacity: 0;
    animation-fill-mode: forwards;
    animation-delay: 0.5s;
    line-height: 1;  /* 調整行高 */
}

.hero-text-right {
    display: flex;
    flex-direction: column;
    gap: 15px;  /* 調整垂直間距 */
    justify-content: center;  /* 垂直置中 */
}

.hero-text-small {
    font-size: 3.5rem;
    margin: 0;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    animation: slideInLeft 1s ease-out;
    opacity: 0;
    animation-fill-mode: forwards;
    animation-delay: 1s;
    line-height: 1.2;  /* 調整行高 */
}

.hero-subtitle {
    font-size: 3.0rem;
    margin: 0;
    padding: 5px 15px;
    display: inline-block;
    color: white;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
    animation: slideInLeft 1s ease-out, glowing 2s infinite;
    opacity: 0;
    animation-fill-mode: forwards;
    animation-delay: 1.5s;
    line-height: 1.2;  /* 調整行高 */
}

/* 滑入動畫 */
@keyframes slideInLeft {
    from {
        transform: translateX(-100px);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}

/* 修改發光效果為白色，移除閃爍效果，使用固定陰影 */
@keyframes glowing {
    0%, 50%, 100% {
        text-shadow: 0 0 10px rgba(255, 255, 255, 0.6);
    }
}

/* 背景漸層效果 */
.home-hero::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.5),
        rgba(0, 0, 0, 0.3)
    );
    z-index: 1;
    animation: gradientShift 8s infinite alternate;
}

@keyframes gradientShift {
    from {
        background: linear-gradient(
            to right,
            rgba(0, 0, 0, 0.5),
            rgba(0, 0, 0, 0.3)
        );
    }
    to {
        background: linear-gradient(
            to right,
            rgba(0, 0, 0, 0.3),
            rgba(0, 0, 0, 0.5)
        );
    }
}

.hero-content {
    color: white;
    z-index: 2;
    position: relative;
}

/* 滾動提示 */
.scroll-indicator {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    animation: bounce 2s infinite;
    z-index: 2;
    color: white;
    cursor: pointer;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-30px);
    }
    60% {
        transform: translateY(-15px);
    }
}

/* Mission Section */
.mission-section {
    padding: 80px 10%;
    text-align: center;
    background-color: #f5f5f5;
}

.mission-section h2 {
    color: #333;
    margin-bottom: 30px;
}

.mission-section p {
    max-width: 800px;
    margin: 0 auto;
    line-height: 1.6;
}

/* Top decorative container */
.mission-top-decor {
  position: absolute;
  top: 50px;
  left: 0;
  right: 0;
  height: 2px;
}

/* Top-left corner */
.mission-top-decor::before {
  content: '';
  position: absolute;
  left: 20%;  /* 改用百分比 */
  top: -15px;
  width: 30px;
  height: 30px;
  border-left: 2px solid #A58A72;
  border-top: 2px solid #A58A72;
}

/* Bottom decorative container */
.mission-bottom-decor {
  position: absolute;
  bottom: 30px;
  left: 0;
  right: 0;
  height: 2px;
}

/* Bottom line */
.mission-bottom-decor::before {
  content: '';
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 120px;
  height: 2px;
  background-color: #A58A72;
  bottom: -15px;
}

/* Bottom-right corner */
.mission-bottom-decor::after {
  content: '';
  position: absolute;
  right: 20%;  /* 改用百分比 */
  bottom: -15px;
  width: 30px;
  height: 30px;
  border-right: 2px solid #A58A72;
  border-bottom: 2px solid #A58A72;
}

.mission-title {
  font-size: 3rem;
  color: #A58A72;
  margin-bottom: 2rem;
  font-family: 'Inter', sans-serif;
  font-weight: 700;
}

.mission-text {
  font-size: 1.5rem;
  line-height: 1.8;
  color: #142613;
  max-width: 800px;
  margin: 0 auto;
  font-family: 'Inter', sans-serif;
  font-weight: 600;
}

