  }

  /* Mission section adjustments */
  .mission-section {
    padding: 5rem 2rem;
  }

  .mission-content {
    padding: 3rem;
  }

  .mission-title {
    font-size: 2rem;
  }

  .mission-text {
    font-size: 1.2rem;
    line-height: 1.6;
    padding: 0 1rem;
  }
}

/* For Mobile Phones */
@media screen and (max-width: 499px) {
  /* Navbar mobile styles */
  .navbar {
    padding: 0.8rem;
  }

  .navbar-logo {
    width: 100px;  /* Even smaller logo for mobile */
  }

  .nav-links {
    font-size: 0.8rem;
    gap: 0.8rem;
  }

  /* Hero section mobile adjustments */
  .hero-text-large {
    font-size: 3rem;  /* Much smaller for mobile */
  }

  .hero-text-small {
    font-size: 2rem;
  }

  .hero-subtitle {
    font-size: 2rem;
  }

  /* Mission section mobile adjustments */
  .mission-section {
    padding: 3rem 1rem;
  }

  .mission-content {
    padding: 1.5rem;
  }

  .mission-title {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
  }

  .mission-text {
    font-size: 1rem;
    line-height: 1.5;
  }

  /* Adjust decorative elements */
  .mission-top-decor::before,
  .mission-bottom-decor::after {
    width: 15px;
    height: 15px;
  }

  .mission-bottom-decor::before {
    width: 60px;  /* Shorter line for mobile */
  }
}

/* For very small devices */
@media screen and (max-width: 320px) {
  .navbar-logo {
