        font-weight: 800;
        text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.6);
    }
    
    .hero-text-right {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 8px;
    }
    
    .hero-text-small {
        font-size: 2.5rem;
        font-weight: 700;
        line-height: 1;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
    }
    
    .hero-subtitle {
        font-size: 2rem;
        padding: 3px 0;
        line-height: 1;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
    }
    
    /* 加強背景效果 */
    .home-hero::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(
            to right,
            rgba(0, 0, 0, 0.6),
            rgba(0, 0, 0, 0.4)
        );
        z-index: 1;
    }
}

/* 平板尺寸調整 */
@media (max-width: 768px) {
    .hero-text-large {
        font-size: 5rem;
    }
    
    .hero-text-small {
        font-size: 2.2rem;
    }
    
    .hero-subtitle {
        font-size: 1.8rem;
    }
}

/* 手機尺寸調整 */
@media (max-width: 480px) {
    .hero-text-group {
        gap: 10px;
    }
    
    .hero-text-large {
        font-size: 4.5rem;
    }
    
    .hero-text-small {
        font-size: 1.8rem;
    }
    
    .hero-subtitle {
        font-size: 1.5rem;
    }
}

/* 小型手機調整 */
@media (max-width: 380px) {
    .hero-text-group {
        gap: 8px;
    }
    
    .hero-text-large {
        font-size: 4rem;
    }
    
    .hero-text-small {
        font-size: 1.5rem;
    }
    
    .hero-subtitle {
        font-size: 1.2rem;
    }
}